import React from 'react'
import './App.css';
import firebase from "./firebase";
function App() {
  const [_trangthai,Settrangthai] = React.useState('')
  const GetData = ()=>{
    firebase.ref('dulieu/trangthai').on('value', function (snapshot) {
      Settrangthai(snapshot.val())
  });
  }
  React.useEffect(()=>{
    GetData()
  },[])
  console.log(_trangthai)
  return (
    <div className="App">
      <header className="App-header">
            <div className="text-center">
                <label><h3>{_trangthai ==='1' ? 'Hiện tại đang mưa' : 'Hiện tại không mưa'}</h3></label>
            </div>
      </header>
    </div>
  );
}

export default App;
